.Main {
    background-color: rgb(18, 165, 116);
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    

} 
.current-time{
    height: 10vh;

}
.circle{
    display: flex;
    justify-content: center;
    align-items: center;;
    height: 40vw;
    width: 40vw;
    background-color: pink;
    border-radius: 50%;
}
.circle-red{
    background-color: orange;
}
.time-style{
    font-size: 10vw
}
.buttonStyle{
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-around;
}
.small-circle{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    height: 23vw;
    width: 23vw;
    background-color: lightgreen;
}
p{
    text-align: center;
}
